import { useEffect, useState } from "react";

interface AnimeDetailProps {
  id: string;
}

const AnimeDetail: React.FC<AnimeDetailProps> = ({ id }) => {
  const [anime, setAnime] = useState<any>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(false);

  useEffect(() => {
    const loadAnime = async () => {
      setLoading(true);
      try {
        const res = await fetch(`https://api.jikan.moe/v4/anime/${id}`);
        const json = await res.json();
        setAnime(json.data);
      } catch (e) {
        setError(true);
      } finally {
        setLoading(false);
      }
    };

    loadAnime();
  }, [id]);

  if (loading) return <p>Carregando...</p>;
  if (error) return <p>Erro ao carregar anime.</p>;
  if (!anime) return <p>Anime não encontrado.</p>;

  return (
    <div
      style={{
        position: "relative",
        textAlign: "center",
        zIndex: 2,
        backgroundColor: "transparent", // Corrige o fundo branco
      }}
    >
      <img
        src={anime.images?.jpg?.image_url}
        alt={anime.title}
        style={{
          width: "250px",
          height: "auto",
          borderRadius: "8px",
          objectFit: "cover",
          marginBottom: "20px",
          display: "block", // Garante que a imagem não terá margens extras
          marginLeft: "auto", 
          marginRight: "auto", // Centraliza a imagem
        }}
      />
      <h1>{anime.title}</h1>
      <p><strong>Score:</strong> {anime.score || "N/A"}</p>
      <p><strong>Status:</strong> {anime.status || "N/A"}</p>
      <p><strong>Descrição:</strong> {anime.synopsis || "Sem descrição."}</p>
    </div>
  );
};

export default AnimeDetail;
